	module.exports = {"data":{"markdownRemark":{"html":"<p>Elasticsearch and Kibana are pretty popular way to store, visualise and analyze data from your application. In this post I want to provide a fast and easy guide how to setup Elasticsearch and Kibana on Docker. I will not go deep with each parameter explanation since I don't want begginers to be flooded with stuff they don't need or understand.</p>\n<p>Pretty much the only requirement is to have <a href=\"https://www.docker.com/products/docker-desktop\">Docker</a> installed.</p>\n<p>First step is pulling Elasticsearch and Kibana images. </p>\n<pre><code class=\"language-bash\">docker pull docker.elastic.co/elasticsearch/elasticsearch:7.6.2\ndocker pull docker.elastic.co/kibana/kibana:7.6.2\n</code></pre>\n<p>Second step is to create network for our containers because Kibana needs to be able to connect to Elasticsearch to get data it needs. In official docs you can see how to skip this stepp using <code>--link</code> parameter but that parameter is deprecated and can be removed anytime.</p>\n<pre><code class=\"language-bash\">docker network create elastic\n</code></pre>\n<p>Then we create containers.</p>\n<pre><code class=\"language-bash\">docker container create -p 9200:9200 -p 9300:9300 --name=elasticsearch --network=elastic -e \"discovery.type=single-node\" docker.elastic.co/elasticsearch/elasticsearch:7.6.2\ndocker container create --name=kibana --network=elastic -p 5601:5601 docker.elastic.co/kibana/kibana:7.6.2\n</code></pre>\n<p>Finally only thing we just need to start both containers. After a minute just go to <a href=\"http://localhost:5601\">localhost:5601</a>.</p>\n<pre><code class=\"language-bash\">docker start elasticsearch\ndocker start kibana\n</code></pre>\n<p>And we should see the result</p>\n<img style=\"width:100%\" src=\"/img/kibana_welcome_screen.png\" title=\"Kibana\" alt=\"Kibana welcome screen\" />","id":"/Users/martinvich/repos/blog/src/pages/posts/elasticsearch-and-kibana-docker-install.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"How to instal Elasticsearch and Kibana on Docker","date":"April 13, 2020","path":"/how-to-install-elastic-search-and-kibana","tags":["Elasticsearch","Kibana","Docker"]}}},"pathContext":{"siteTitle":"Martin Vích's blog","pathValue":"/how-to-install-elastic-search-and-kibana"}}

